.calculator-tile {
  background-color: #f9f9f9; /* Light gray background */
  padding: 20px;
  border-radius: 15px;
  border: 2px solid #007bff; /* Blue border */
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.3); /* Blue shadow */
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1; /* Make tiles square */
}

.calculator-tile:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 123, 255, 0.5); /* Stronger blue shadow on hover */
}

.icon-container {
  margin-bottom: 15px; /* Space between icon and title */
}

.calculator-tile h3 {
  margin: 0 0 10px;
  color: #333;
}

.calculator-tile p {
  margin: 0;
  color: #555;
}